'use strict';

/**
* Test if a value is a positive integer.
*
* @module @stdlib/assert-is-positive-integer
*
* @example
* import isPositiveInteger from '@stdlib/assert-is-positive-integer' ;
*
* var bool = isPositiveInteger( 5.0 );
* // returns true
*
* bool = isPositiveInteger( new Number( 5.0 ) );
* // returns true
*
* bool = isPositiveInteger( -5.0 );
* // returns false
*
* bool = isPositiveInteger( 3.14 );
* // returns false
*
* bool = isPositiveInteger( null );
* // returns false
*
* @example
* import { isPrimitive as isPositiveInteger } from '@stdlib/assert-is-positive-integer' ;
*
* var bool = isPositiveInteger( 3.0 );
* // returns true
*
* bool = isPositiveInteger( new Number( 3.0 ) );
* // returns false
*
* @example
* import { isObject as isPositiveInteger } from '@stdlib/assert-is-positive-integer' ;
*
* var bool = isPositiveInteger( 3.0 );
* // returns false
*
* bool = isPositiveInteger( new Number( 3.0 ) );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import isPositiveInteger from './main.js' ;
import isPrimitive from './primitive.js' ;
import isObject from './object.js' ;


// MAIN //

setReadOnly( isPositiveInteger, 'isPrimitive', isPrimitive );
setReadOnly( isPositiveInteger, 'isObject', isObject );


// EXPORTS //

export default isPositiveInteger;
